$('.card-item-body').hide();// 將內容隱藏
$('.card-item').eq(0).addClass('active'); // 調整上下箭頭
$('.card-item-body').eq(0).show();// 預設第一個內容顯示

$('.card-item').click(function (e){
  e.preventDefault();
  $(this).toggleClass('active').siblings().removeClass('active');//取消其他箭頭方向
  $(this).children().next().slideToggle();
  $(this).siblings().children().next().slideUp();
})